body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /*align-items: stretch;*/
  /*justify-content: flex-end;*/
  font-size: calc(40px + 2vmin);
}

.Links {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.Link-icon-image {
  padding: 5px;
  height: 50px;
  width: 50px;
}

.Link-icon-image:hover {
  opacity: 0.5;
}
.Home-header {
  background-color: #282c33;
  flex:1 1;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.Home-body {
  background-color: #ffffff;
  flex: 15 1;
  color: #282c33;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.Projects {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.Project-main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-width: 1px;
  border-color: #282c33;
  border-radius: 10px;
  border-style: solid;
  padding: 20px;
  margin: 20px;
}

.Project-title {
  font-size: 30px;
  margin: 0px;
}

.Project-description {
  font-size: 20px;
  margin: 0px;
}

.Project-links {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.Project-icon-image {
  padding: 5px;
  height: 25px;
  width: 25px;
}

.Project-icon-image:hover {
  opacity: 0.5;
}
